    id: "26",
    date: "3/6/2022 5:33 PM",
    title: "Weekend R&D",
    subtitle: "Tabletop Games",
    text: [
      "Ok, so the other day I had a very very good interview with a company I would really really like to work for. Cards on the table, I suck at lying anyway, so why try?",
      "In my haste to answer their question I feel like I wasn't as granular as I should have been or as precise as I prefer to be. So I've been mulling over what to say to clarify this weekend. I believe I hinted that i would probably have a clearer picture after getting a few days to spin up the problem. That's how I am. A quick answer often gets revised. I used to be editor in chief of my high school paper. That baby had a lot of drafts. It's one of my strengths, but my weakness is speaking too soon and too declaritively.",
      "So to rectify this I wanted to double check that I spoke correctly there and actually go through the problem from a new angle.",
      "Here's the thing I'm thinking - I'm a layman. I'm not the DM. Normally I would think of this as a detriment, but I am reminded of that 99% invisible podcast where they talk about curb cuts. https://99percentinvisible.org/episode/curb-cuts/transcript. They talk about how curb cuts end up being good for everyone, not just people in wheelchairs. It's great for the people in wheelchairs, and also people with strollers, people on bikes. I know a guy who uses the final fantasy hearing assistance add on becuase it helps him know when he has been hit more easily. Accessibility is a great thing to have, especially when paired with the option to have it your way. I think it's that way for people who lazily skim character sheets, like me. I'm motivated to make it easier, because I have a short attention span and knows what keeps my attention and what doesn't.",
      "Also a weird underdog selling point - I don't want to repeat myself. I live in the forest but I want my code to be like the torrid AZ desert I grew up in - DRY. Bill Gates said 'I choose a lazy person to do a hard job. Becasue a lazy person will find an easy way to do it.' DRY is the lazy man's best friend. It means you can iterate quickly if you can identify parts of these games that are like parts of other games. It gives you a jumping off point for standardization.",
      "So essentially - the question isn't how you would design an individual character sheet so much as how you would assemble the component parts of ANY character sheet or ALL character sheets.",
      "Luckily, they have a UI/UX designer of repute, so that makes things incredibly easier. I kind of like constraints. What I imagine would be the approach here would be to start mobile first on the redesign but offer legacy options as well for those who don't want to make the switch immediately or are quite all right with the current design. With modern libraries (React and contemporaries), not hard to provide both options.",
      "I was asked how I would go through a game if I were to build the character design for a tabletop game - any tabletop game. This is a brilliant approach because realistically I know they need scale to do what they are doing. They now have a lot of intellectual properties that are going to need support and going to need to be able to get to MVP quickly and fast. This platform is already well regarded in their sphere and are doing a lot right. But in the tech age, they need agile mobile first design and encapsulation. At least, this is how I'm understanding the problem.",
      "So I picked up Monster of the Week and started looking through the sheet to see some things it shared and differed from other games of its type.",
      "So what are some examples of reusable design?",
      "D&D and Cyberpunk RED for instance both have skills that are rolled that can affect other skills as modifiers. So they have a standalone stat and a modifier stat that gets added to skills.",
      "I was taking a look at Monster of the Week today and saw that their moves are similar to D&D in that they are invoked upon command and then have dice rolls that work on modifiers. We consolidate by making a spells/moves class. The Gear or weapons also should be their own reusable stat - all three of those games share that.",
      "If there were to be another FATE-like or Fiasco type game, the onboarding of Monster of the Week would also fit the bill for this, for instance since it involves people going around the table and filling in their relationships with others.",
      "The luck, harm, and experience counters remind me a bit of cthulian horror games in their similarity to a 'sanity' check and I imagine this is somewhat what the designers intended on this. (Haven't hardcore done my research - but this is from skimming the first player section from an hour so cut me some slack.)",
      "Some things, like choosing your alignment in D&D or choosing your archetype in Monster Hunter or even Cyberpunk RED are done just once. That's onboarding.",
      "As a lazy layman who has a short attention span, this made me consider that if it has not already been a consideration, it may be wise for the team when thinking of sweeping design changes to think to opportunities to simplify ease of use for onboarding characters. The best apps I have used for character creation have tried to keep things simple.",
      "The one that stands out to me as a shining example is the Cyberpunk RED companion. I have used that pretty much since it came out and though it had a few glitches to start (typing was one) the app had a very nice way of walking you through the problem of getting set up in Cyberpunk. It may not have every expansion, but it did a great job of giving you a convenient way to see your character, add and remove items, showing stats, and rolling for you on command and adding modifiers.",
      "There are things that a computer is better at than a human. Adding numbers is one, calculating rolls is another. Opportunities here for people to pick whether they see this roll or not or see the modifiers or not. Some people don't want to see them and want to see this on the back end, some people want to see them all of the time, and some people want to see tehm when they want to see them but otherwise hide them. So you want to write some equations that take this into account, classify them and document well what these things do in a centralized location so that as you are designing a character sheet for a new game you have a bit of a library to choose from of similar options and then plug in the options. Much the same way SCSS works, or even the React library iteself. It's all that OOP/class architechture stuff at its core.",
      "Anyway, now that I've written out all of these thoughts I'm utterly exhausted. I'll rework this probably as I go throughout the next few days, but I wanted to get out a few key things that stood out to me as I have been mulling over the problem so far. I definitely know that it is going back into the oak barrell for a few days before I'm ready to break it down succinctly though.",
    ],
    tags: [
      "tabletop",
      "monsteroftheweek",
      "charactersheets",
      "D&D",
      "CyberpunkRED",
      "OOP",
      "encapsulation",
      "polymorphism",
    ],
    photos: [{}],
